/** @ngInject */
function routesConfig($stateProvider, $urlRouterProvider, $locationProvider) {
  $locationProvider.html5Mode(false).hashPrefix('!');
  $urlRouterProvider.otherwise('/');

  $stateProvider
    .state('login', {
      url: '/',
      component: 'login'
    })
    .state('home', {
      url: '/accueil',
      component: 'home'
    })
    .state('benevoles', {
      url: '/benevoles',
      component: 'benevoles'
    })
    .state('missions', {
      url: '/missions',
      component: 'missions'
    })
    .state('inscriptions', {
      url: '/inscription',
      component: 'inscriptions'
    });
}
